import React, { useContext } from 'react';
import { ProductosContext } from '../../Context/Producto';
interface Props {
    change: (r: string) => void,
    defaultValue: string,
    width?: string
    tipo?: 'TODOS LOS ' | 'SELECCIONAR'
}
export default function DepartamentoSelector({ change, defaultValue, width, tipo = 'TODOS LOS ' }: Props) {
    const context = useContext(ProductosContext)
    const setRol = (Rol: React.ChangeEvent<HTMLSelectElement>) => { change(Rol.currentTarget.value) }
    return <select onChange={setRol} className="form-control" value={defaultValue}>
        <option value='TODO'>{tipo} DEPARTAMENTO</option>
        {(context !== null) ? context.Departamentos.map(c => {
            return <option key={"user" + c.codigo} value={c.codigo}>{c.descripcion}</option>
        }) : <></>}
    </select>
}