import React, { useState } from "react";
import { Table } from "react-bootstrap";
import Paginacion from "./Paginacion";
interface Props {
    itens: any[]
    mostrar: number
    row: (d: any) => any
    col: number
    children: any
}
export default function Body(p: Props) {
    const [page, setPage] = useState(1);
    const key = parseInt((Math.random() * (500000 - 1)).toFixed());
    function body() {
        let init = (page - 1) * p.mostrar;
        let fin = init + p.mostrar;
        let itens = p.itens.filter((iten: any, index: number) => (index >= init && index < fin))
        let pts = itens.map((iten: any) => p.row(iten))
        return pts
    }
    return <Table striped hover >
        <thead>
            {p.children}
        </thead>
        <tbody key={'mostrar' + key}>
            {body()}
        </tbody>
        <tr>
            <td colSpan={p.col}>
                <Paginacion page={page}
                    length={p.itens.length}
                    mostrar={p.mostrar}
                    setpage={setPage} />
            </td>
        </tr>
    </Table>
}